export default[
  { path: '/login', component: () => import('@/components/login') },

  {
    path: '/',
    name: '/',
    component: () => import('@/components/main.vue'),
    children: [
      { path: '/index', component: () => import('@/components/index') },
      { path: '/form', component: () => import('@/components/demo/form') },
      { path: '/table', component: () => import('@/components/demo/table') },
      { path: '/transfer', component: () => import('@/components/demo/transfer') },
      { path: '/echart', component: () => import('@/components/demo/echart') },
      { path: '/menuMgt', component: () => import('@/components/systemMgt/menuMgt') },
      { path: '/roleMgt', component: () => import('@/components/systemMgt/roleMgt') },
      { path: '/userMgt', component: () => import('@/components/systemMgt/userMgt') }
    ]
  }
]



