axios.interceptors.request.use(function (config) {
  config.headers.token = sessionStorage.getItem('user_token')// 将接口返回的token信息配置到接口请求中
  return config
}, function (error) {
  return Promise.reject(error)
})

const configPath = []
const type = 'release'
switch (type) {
  case 'locality' :
    configPath[0] = 'static/api/'
    configPath[2] = '.json'
    break
  case 'release':
    configPath[0] = 'http://172.22.154.225:8080/'
}
export default{
  promise: function (config) {
    config.method = config.method || 'post'
    if (configPath[2]) {
      config.method = 'get'
    }
    configPath[1] = config.url
    config.url = configPath.join('')
    return axios(config)
  },
  // promise: function (config) {
  //   config.method = config.method || 'post'
  //   if (configPath[2]) {
  //     config.method = 'get'
  //   }
  //   configPath[1] = config.url
  //   config.url = configPath.join('')
  //   return axios(config)
  // },
  // 手动加载JS
  reloadAbleJSFn: function (id, newJS) {
    let oldjs = null
    oldjs = document.getElementById(id)
    if (oldjs) oldjs.parentNode.removeChild(oldjs)
    var scriptObj = document.createElement('script')
    scriptObj.src = newJS
    scriptObj.type = 'text/javascript'
    scriptObj.id = id
    document.getElementsByTagName('head')[0].appendChild(scriptObj)
  },
  // 计算table高度
  getHeightFun: function (delHei) {
    return document.body.offsetHeight - delHei - 280
  },
  setCookie: function (name, value, day) {
    var date = new Date()
    date.setDate(date.getDate() + day)
    document.cookie = name + '=' + value + ';expires=' + date
  },
  getCookie: function (name) {
    var reg = RegExp(name + '=([^;]+)')
    var arr = document.cookie.match(reg)
    if (arr) {
      return arr[1]
    } else {
      return ''
    }
  },
  delCookie: function () {
    this.setCookie(name, null, -1)
  }

}



